import { _decorator, Component, EventHandler, Label, Node } from 'cc';
import { ScrollViewPlusItem } from './ScrollViewPlusItem';
const { ccclass, property } = _decorator;

@ccclass('item3')
export class item3 extends ScrollViewPlusItem {
    
    @property(Label)
    nameLabel:Label=null;

    @property(Label)
    descLabel:Label=null;

    

    public setValue(name:string, desc:string){
        this.nameLabel.string=name;
        this.descLabel.string=desc;

    }


}

